import { Base, IContent, IGroup, IPos, ISize, LayoutItemTypeEnum } from '../base'
import { ImgPlaceHolder, Char, Formula } from './'

/** 限制下TextGroup的child 类型 */
export type TextGroupChild = Char | Formula | ImgPlaceHolder

export class TextGroup extends Base implements IGroup, IContent {
  layoutItemType: LayoutItemTypeEnum = LayoutItemTypeEnum.TEXT_GROUP
  canLineBreak: boolean = false

  groups: TextGroupChild[] = []

  rawContent: string = ''
  content = ''

  getPos(): IPos | Promise<IPos> {
    throw new Error('Method not implemented.')
  }

  getSize(): ISize | Promise<ISize> {
    throw new Error('Method not implemented.')
  }
}
